	module.exports = {"data":{"markdownRemark":{"html":"<p>Here's this from global-minded site about broadband technologies:</p>\n<blockquote>\n\"And now for the fun part&#8230;.\n<p>Did you know that Iceland is only behind South Korea, Netherlands and Denmark in Internet penetration, and 84% of its households have Internet connection?</p>\n<p>Australia adds 40,000 broadband connections every month.</p>\n<p>In Spain, you can get a 20 megabit/second connection for $36 a month and that includes free phone service and a wifi router from a company called Jazztel.</p>\n<p>Nearly 60% of Indians get their broadband at cyber cafes that dot the country?</p>\n<p>Uruguay’s telecom monopoly sells a 512/256 kbps connection for $190 a month.\"</p>\n</blockquote>\nRead it: <a href=\"http://gigaom.com/2005/10/13/five-fun-facts-from-broadband-wiki/\">Five fun facts from Broadband Wiki</a>\n<p>(Found on:<a href=\"http://gigaom.com\">Om Malik's Broadband Blog</a>.)</p>","frontmatter":{"created_at":"November 12, 2005","path":"/2005/11/12/five-fun-facts-from-broadband-wiki","title":"Five fun facts from Broadband Wiki","tags":["broadband","india"]}}},"pathContext":{}}

